.flexed {
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
	gap:20px;
	margin-top:20px;
	justify-content: center;
}

.flexed2 {
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
	justify-content: center;
	margin-bottom: 0px;
}

.grid-box {
	display: grid;
	grid-template-columns: 1fr;
	justify-content: center;
	align-content: center;
	align-items: center;
}

h4 {
	margin-top: 0px;
	cursor: pointer;
}